import React from "react";

const LoaderContainer = () => {
  return (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        zIndex: 1000,
        background: " #0000003a",
        transition: "opacity 0.2s ease",
      }}
    ></div>
  );
};

export default LoaderContainer;
